import { connect } from 'react-redux';
import Account from './components/feature';
import {
  getSelectedAccount,
  getSelectedAccountNumber,
} from '../../redux/accounts/accounts.selectors';
import { toggleModal } from '../../redux/accounts/accounts.actions';
import { activateAccountsTab } from '../../redux/menu/menu.actions';
import { getSignaletic } from '../../redux/accounts/accounts.actions';

const mapStateToProps = state => ({
  accounts: state.Accounts.accounts,
  selectedAccount: getSelectedAccount(state),
  selectedAccountId: state.Accounts.selectedAccountId,
  activeTab: state.Menu.accountsActiveTab,
  selectedAccountNumber: getSelectedAccountNumber(state),
});
const mapDispatchToProps = dispatch => ({
  toggleModal: () => {
    dispatch(toggleModal());
  },
  activateTab: activeTab => {
    dispatch(activateAccountsTab(activeTab));
  },
  getSignaletic: () => {
    dispatch(getSignaletic());
  },
});
export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Account);
