import React from 'react';
import Panel from '../../../components/Panel/Panel';
import { t } from '../../../i18n';
import { connect } from 'react-redux';
import { getOperations } from '../../../redux/operations/operations.actions';
import FormatDate from '../../../components/FormattedDate/FormattedDate';
import FormattedNumber from '../../../components/FormattedNumber/FormattedNumber';
import FormatsConfiguration from '../../../utils/FormatsConfiguration';
import OperationTable from '../../../components/DataTable/CustomDataTable';
import DateCalender from '../../../components/DateCalendar/DateCalendar';
import { dateFilter } from '../../../utils/DateUtils';
import OperationsTemplate from '../../../pdf/Templates/OperationsTemplate';
import { exportTableToPdf, exportTableToExcel } from 'utils/exportUtils';
import OperationsTemplateExcel from '../../../excel/Templates/OperationsTemplateExcel';
class Operations extends React.PureComponent {
  constructor() {
    super();
    let maxDate = new Date();
    this.state = {
      loading: true,
      totalRecords: 0,
      maxDate: maxDate,
    };
  }

  _dateChangeHandler = () => {
    this.forceUpdate();
  };

  componentDidMount() {
    this.props.getOperations();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.selectedAccountId !== this.props.selectedAccountId)
      this.props.getOperations();
  }
  _buildDataTableRows = () =>
    this.props.operations.map(data => {
      const { date, valueDate, description, code, amount, sign } = data;
      return {
        date: date,
        dateValue: valueDate,
        description: description,
        code: code,
        amount: amount,
        sign: sign,
      };
    });
  _dateTemplate = rowData => {
    if (rowData.sign === 'C') {
      return (
        <FormatDate
          dateTime={rowData.date}
          format={FormatsConfiguration.DateTimeFormat.fullDate}
          className="positif_transaction"
        />
      );
    } else {
      return (
        <FormatDate
          dateTime={rowData.date}
          format={FormatsConfiguration.DateTimeFormat.fullDate}
          className="negative_transaction"
        />
      );
    }
  };
  _dateValueTemplate = rowData => (
    <FormatDate
      dateTime={rowData.dateValue}
      format={FormatsConfiguration.DateTimeFormat.fullDate}
    />
  );
  _amountTemplate = rowData => {
    if (rowData.sign === 'C') {
      return (
        <span style={{ color: 'green' }}>
          <FormattedNumber
            number={rowData.amount}
            positive
            format={FormatsConfiguration.AmountFormat}
          />
        </span>
      );
    } else {
      return (
        <span style={{ color: 'red' }}>
          <FormattedNumber
            number={rowData.amount}
            negative
            format={FormatsConfiguration.AmountFormat}
          />
        </span>
      );
    }
  };
  _dateCalendarComponent = maxDate => (
    <DateCalender
      maxDate={maxDate}
      handleDateChange={e => {
        this.dt.filter(e.value, 'date', 'custom');
        this._dateChangeHandler();
      }}
    />
  );

  _getPdfFileName = () => `Historique_${this.props.selectedAccountId}.pdf`;
  _getSheetName = () => `Historique_${this.props.selectedAccountId}`;

  render() {
    let rows = this._buildDataTableRows();
    return (
      <div className="page-container_inner">
        <div className="main-content ">
          <div className="row mx-auto">
            <div className="col-sm-12">
              <OperationsTemplate tableRows={rows} />
              <OperationsTemplateExcel tableRows={rows} />
              <Panel
                title={t('Accounts:operationsHistory')}
                hasDownloadLink
                pdfLinkAction={() =>
                  exportTableToPdf(
                    'operationsSummary',
                    'operationsLogo',
                    'operationsData',
                    this._getPdfFileName(),
                  )
                }
                csvLinkAction={() =>
                  exportTableToExcel('Excel', 'EXCEL', this._getSheetName())
                }
                selectedAccountNumber={this.props.selectedAccount}
              >
                <OperationTable
                  value={rows}
                  onRowReorder={e => (rows = e.value)}
                  reference={el => (this.dt = el)}
                  columnResizeMode="fit"
                  cols={[
                    {
                      bodyClassName: 'td-dateclass',
                      field: 'date',
                      header: t('Accounts:date'),
                      body: this._dateTemplate,
                      filter: true,
                      filterElement: this._dateCalendarComponent(
                        this.state.maxDate,
                      ),
                      filterFunction: dateFilter,
                    },
                    {
                      field: 'dateValue',
                      header: t('Accounts:dateValue'),
                      body: this._dateValueTemplate,
                      filter: true,
                      filterElement: this._dateCalendarComponent(
                        this.state.maxDate,
                      ),
                      filterFunction: dateFilter,
                    },
                    {
                      field: 'description',
                      header: t('Accounts:label'),
                      className: 'small-col',
                    },
                    {
                      field: 'code',
                      header: t('Accounts:reference'),
                    },
                    {
                      field: 'amount',
                      header: t('Accounts:amount', {
                        currency: t('Components:currency'),
                      }),

                      body: this._amountTemplate,
                    },
                  ]}
                />
              </Panel>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getOperations: accountId => {
    dispatch(getOperations(accountId));
  },
});

const mapStateToProps = state => ({
  operations: state.Operations.operations,
  selectedAccountId: state.Accounts.selectedAccountId,
  signaletic: state.Accounts.signaletic,
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Operations);
