//@flow
import React from 'react';
import moment from 'moment';

type Props = {
  className?: string,
  dateTime: string,
  format: string,
};

const formatDateTime = (dateTime, format) => {
  if (!dateTime) return null;
  return moment(dateTime).format(format);
};

const FormatDateComponent = (props: Props) => (
  <div className={props.className}>
    {formatDateTime(props.dateTime, props.format)}
  </div>
);

export default FormatDateComponent;
