import React, { Component } from 'react';
import { Calendar } from 'primereact/calendar';
import classes from './DateCalendar.module.css';
const BackDrop = props =>
  props.show ? (
    <div className={classes.BackDrop} onClick={props.clicked} />
  ) : null;
export class DateCalendar extends Component {
  constructor() {
    super();
    this.state = {
      dates: null,
      show: false,
      selectedDates: null,
    };
  }

  render() {
    const Language = {
      dayNames: [
        'Dimanche',
        'Lundi',
        'Mardi',
        'Mercredi',
        'Jeudi',
        'Vendredi',
        'Samedi',
      ],
      dayNamesShort: ['Dim', 'lun', 'mar', 'Mer', 'Jeu', 'Ven', 'Sam'],
      dayNamesMin: ['D', 'L', 'M', 'M', 'J', 'V', 'S'],
      monthNames: [
        'Janvier',
        'Février',
        'Mars',
        'Avril',
        'May',
        'Juin',
        'Juillet',
        'Aout',
        'Septembre',
        'Octobre',
        'Novembre',
        'Decembre',
      ],
      monthNamesShort: [
        'Jan',
        'Fev',
        'Mar',
        'Avr',
        'Mai',
        'Jun',
        'Jul',
        'Aou',
        'Sep',
        'Oct',
        'Nov',
        'Dec',
      ],
      today: "Aujourd'hui",
      clear: 'Vider',
      weekHeader: 'SM',
    };
    let CalendarClass = `${this.state.show ? 'd-block' : ''}`;
    return (
      <>
        <BackDrop
          show={this.state.show}
          clicked={() => {
            this.setState({ show: false });
          }}
        />
        <span
          className="icon-calendrier"
          onClick={() => {
            this.setState({ show: !this.state.show });
          }}
        ></span>
        <div>
          <Calendar
            locale={Language}
            readOnlyInput={true}
            dateFormat="dd/mm/yy"
            className={CalendarClass}
            value={this.state.dates}
            onChange={e => {
              this.setState({ dates: e.value });
              this.props.handleDateChange(e);
            }}
            selectionMode="multiple"
            showIcon={true}
            maxDate={this.props.maxDate}
            showButtonBar={true}
          />
        </div>
      </>
    );
  }
}

export default DateCalendar;
