import React from 'react';
import { connect } from 'react-redux';
import { pdfStyles } from './PdfTemplate.styles';
import { t } from '../../i18n';
import * as DateUtils from '../../utils/DateUtils';
import FormatsConfiguration from '../../utils/FormatsConfiguration';
import FormattedNumber from '../../components/FormattedNumber/FormattedNumber';
import Logo from '../../components/Logo/PNGLogo';
import { getSelectedAccountNumber } from 'redux/accounts/accounts.selectors';

const OperationsTemplate = props => {
  const { selectedAccountNumber, fullName } = props;
  let renderOperations = props.tableRows.map(row => (
    <tr key={row.code}>
      <td>
        {DateUtils.formatDateTime(
          row.date,
          FormatsConfiguration.DateTimeFormat.fullDate,
        )}
      </td>
      <td>{DateUtils.formatDateTime(row.dateValue)}</td>
      <td>{row.code}</td>
      <td>
        <FormattedNumber
          number={row.amount}
          positive={row.sign === 'C'}
          negative={row.sign === 'D'}
          format={FormatsConfiguration.AmountFormat}
        />
      </td>
      <td>
        <p>{row.description}</p>
      </td>
    </tr>
  ));

  return (
    <div id="pdf" style={pdfStyles.pdf}>
      <table id="operationsLogo">
        <tbody>
          <tr>
            <td>
              <Logo />
            </td>
          </tr>
        </tbody>
      </table>
      <table id="operationsSummary">
        <tbody>
          <tr>
            <td>
              {t('Accounts:client')}&nbsp;&nbsp;{fullName} <br />
              {t('Accounts:accountNumber')}&nbsp;&nbsp;
              {selectedAccountNumber}
            </td>
            <td>
              <b>{t('Accounts:accountStatement')}</b>
            </td>
          </tr>
        </tbody>
      </table>
      <table id="operationsData">
        <thead>
          <tr>
            <th>{t('Accounts:date')}</th>
            <th>{t('Accounts:dateValue')}</th>
            <th>{t('Accounts:reference')}</th>
            <th>
              {t('Accounts:amount', {
                currency: t('Components:currency'),
              })}
            </th>
            <th>{t('Accounts:label')}</th>
          </tr>
        </thead>
        <tbody>{renderOperations}</tbody>
      </table>
    </div>
  );
};
const mapStateToProps = state => ({
  selectedAccountNumber: getSelectedAccountNumber(state),
  fullName: state.Accounts.signaletic.fullName,
});

export default connect(
  mapStateToProps,
  null,
)(OperationsTemplate);
