import styled from 'styled-components';
export const FormModalContainer = styled.div.attrs(props => ({
  className: `modal_content modal-container colored-header colored-header-primary modal-effect-10 ${
    props.smallForm ? '' : 'modal-formulaire'
  } ${props.show ? 'modal-show' : ''}`,
}))`
  perspective: none;
  height: 534px;
`;
