import * as jsPDF from 'jspdf';
import 'jspdf-autotable';
import html2canvas from 'html2canvas';

export const exportTableToPdf = (idHeader, idLogo, idTable, filename) => {
  var doc = new jsPDF();
  var width = '30';
  doc.autoTable({
    html: `#${idLogo}`,
    columnStyles: {
      0: { columnWidth: 60 },
    },
    bodyStyles: { minCellHeight: 20 },
    didDrawCell: function(data) {
      if (data.column.index === 0 && data.cell.section === 'body') {
        var td = data.cell.raw;
        var img = td.getElementsByTagName('img')[0];
        var dim = data.cell.height - data.cell.padding('vertical');
        var textPos = data.cell.textPos;
        doc.addImage(img.src, textPos.x, textPos.y, width, dim);
      }
    },
    willDrawCell: function(data) {
      var rows = data.table.body;
      if (data.row.index === rows.length - 1) {
        doc.setFillColor(255, 255, 255);
      }
    },
  });
  doc.autoTable({
    html: `#${idHeader}`,
    headerStyles: {
      0: { columnWidth: 60, halign: 'left', fontSize: 8 },
    },
    columnStyles: {
      0: { columnWidth: 60, halign: 'left', fontSize: 8 },
      1: {
        columnWidth: 70,
        fontSize: 13,
        halign: 'center',
        textColor: 'black',
        font: 'Times New Roman',
      },
      2: { columnWidth: 60, halign: 'left', fontSize: 8 },
    },
    willDrawCell: function(data) {
      var rows = data.table.body;
      if (data.row.index === rows.length - 1) {
        doc.setFillColor(255, 255, 255);
      }
    },
  });

  doc.autoTable({
    html: `#${idTable}`,
    columnStyles: {
      4: { columnWidth: 40 },
    },
    headerStyles: {
      fontSize: 9,
      fillColor: '#e0e0e0',
      textColor: '#505050',
    },
  });
  doc.save(filename);
};

export const exportToPdfUsingCanvas = (id, filename) => {
  const input = document.getElementById(id);
  html2canvas(input).then(canvas => {
    const imgData = canvas.toDataURL('image/png');
    const pdf = new jsPDF();
    const imgProps = pdf.getImageProperties(imgData);
    const pdfWidth = pdf.internal.pageSize.getWidth();
    const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
    pdf.addImage(imgData, 'PNG', 0, 0, pdfWidth, pdfHeight);
    pdf.save(filename);
  });
};

function base64(s) {
  return window.btoa(unescape(encodeURIComponent(s)));
}
function format(s, c) {
  return s.replace(/{(\w+)}/g, function(m, p) {
    return c[p];
  });
}

export const exportTableToExcel = (id, chartname) => {
  var uri = 'data:application/vnd.ms-excel;base64,';
  var template =
    '<html xmlns="urn:schemas-microsoft-com:office:spreadsheet" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns:html="https://www.w3.org/TR/html401/" xmlns="http://www.w3.org/TR/REC-html40"><head><meta charset="UTF-8" /><!--[if gte mso 9]><xml><x:ExcelWorkbook xmlns="urn:schemas-microsoft-com:office:spreadsheet"><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name> <x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions> </x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body> <table>{table}</table></body></html>';
  let table = document.getElementById(id);
  var ctx = { worksheet: chartname || 'Worksheet', table: table.innerHTML };
  window.location.href = uri + base64(format(template, ctx));
};
