//@flow
import React from 'react';

type Props = {
  currency?: string,
  positive?: boolean,
  negative?: boolean,
  number: string | number,
  format: {
    decimals: number,
    groupingBlock: number,
    groupsSeparator: string,
    currencyIsPrefix?: boolean,
  },
};

const formatNumber = (props: Props) => {
  if (!props.number) return null;
  const { decimals, groupsSeparator, groupingBlock } = props.format;
  const regExp = new RegExp(`(\\d)(?=(\\d{${groupingBlock}})+(?!\\d))`, 'g');
  const fixedNumberParts = parseFloat(props.number)
    .toFixed(decimals)
    .toString()
    .split('.');
  fixedNumberParts[0] = fixedNumberParts[0].replace(
    regExp,
    '$1'.concat(groupsSeparator),
  );
  let formattedNumber = fixedNumberParts.join('.');
  if (props.currency) {
    if (props.format.currencyIsPrefix)
      formattedNumber = props.currency.concat(` ${formattedNumber}`);
    else {
      formattedNumber = formattedNumber.concat(` ${props.currency}`);
    }
  }
  if (props.positive) {
    formattedNumber = '+'.concat(formattedNumber);
  } else if (props.negative) formattedNumber = '-'.concat(formattedNumber);
  return formattedNumber;
};

const FormattedNumber = (props: Props) => <>{formatNumber(props)}</>;

export default FormattedNumber;
