import React from 'react';
import { t } from '../../i18n';

type Props = {
  title: string,
  hasButton: boolean,
  buttonText: String,
  buttonAction: Function,
  hasDownloadLink: Boolean,
  pdfLinkTitle: string,
  pdfLinkAction: Function,
  csvLinkTitle: string,
  csvLinkAction: Function,
};

const panel = (props: Props) => (
  <div className="panel panel-default">
    <div className="panel-heading panel-heading-divider">
      <div className="row">
        <div className="col-md-6">{props.title}</div>

        {props.hasButton ? (
          <div className="col-md-6 text-right">
            <button
              className="btn btn-space btn-default md-trigger addform popin_btn"
              data-modal="panelBtn"
              onClick={props.buttonAction}
            >
              <span>{props.buttonText}</span>
            </button>
          </div>
        ) : null}
        <div className="col-md-6">
          {props.hasDownloadLink ? (
            <div className="download_links dropdown_area float_right">
              <span className="purple_download">
                {t('Components:panel.downloadHistory')}
                <button onClick={props.pdfLinkAction} className="btn">
                  {` PDF `}
                </button>
                <button onClick={props.csvLinkAction} className="btn">
                  {` EXCEL `}
                </button>
              </span>
            </div>
          ) : null}
        </div>
      </div>
    </div>
    <div className="panel-body">{props.children}</div>
  </div>
);

export default panel;
