import React from 'react';
import { connect } from 'react-redux';
import { t } from '../../../i18n';
import { exportToPdfUsingCanvas } from '../../../utils/exportUtils';

class Signaletic extends React.PureComponent {
  render() {
    let {
      fullName,
      legalNumber,
      branch,
      mobileNumber,
      birthday,
    } = this.props.signaletic;
    return (
      <div className="page-container_inner">
        <div className="main-content container-fluid">
          <div className="row mx-auto">
            <div className="col-sm-12">
              <div className="panel panel-default">
                <div className="panel-heading panel-heading-divider">
                  <div className="row">
                    <div className="col-md-6">
                      {t('Accounts:signaleticForm')}
                    </div>
                    <div className="col-md-6">
                      <div className="download_links dropdown_area float_right">
                        <span className="purple_download">
                          {t('Accounts:download')} :&nbsp;
                          <button
                            onClick={() =>
                              exportToPdfUsingCanvas(
                                'Details',
                                t('Accounts:idNumber'),
                              )
                            }
                          >
                            PDF
                          </button>
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="panel-body">
                  <div id="Details" className="detail-compte">
                    <div className="row">
                      <div className="col-md-6 col-left">
                        <ul>
                          <li>
                            <span className="title">
                              {t('Accounts:fullName')}
                            </span>
                            <span className="text">{fullName}</span>
                          </li>
                          <li>
                            <span className="title">
                              {t('Accounts:idType')}
                            </span>
                            <span className="text">{t('Accounts:CIN')}</span>
                          </li>
                          <li>
                            <span className="title">
                              {t('Accounts:idNumber')}
                            </span>
                            <span className="text">{legalNumber}</span>
                          </li>
                        </ul>
                      </div>
                      <div className="col-md-6 col-right">
                        <ul>
                          <li>
                            <span className="title">
                              {t('Accounts:phoneNumber')}
                            </span>
                            <span className="text">{mobileNumber}</span>
                          </li>
                          <li>
                            <span className="title">
                              {t('Accounts:birthday')}
                            </span>
                            <span className="text">{birthday}</span>
                          </li>
                          <li>
                            <span className="title">
                              {t('Accounts:agency')}
                            </span>
                            <span className="text">{branch.bank.name}</span>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  signaletic: state.Accounts.signaletic,
});

export default connect(
  mapStateToProps,
  null,
)(Signaletic);
