//@flow

import * as React from 'react';
import { DataTable } from 'primereact/datatable';
import { InputText } from 'primereact/inputtext';
import { Column } from 'primereact/column';
import NoDataSvg from '../../assets/SvgComponents/NoDataSvg/NoDataSvg';
import { t } from '../../i18n';

type Props = {
  value: [],
  cols: [],
  reference: Function,
  onRowReorder: Function,
  columnResizeMode: string,
};
type State = {
  globalFilter: string,
};
const DATATABLE_NAMESPACE = 'Components:datatable.';
class OurDataTableCustom extends React.Component<Props, State> {
  static defaultProps = {
    cols: [],
    columnResizeMode: 'fit',
  };
  state = {
    globalFilter: '',
  };
  render() {
    let NoDataMessage = (
      <>
        <NoDataSvg />
        <div className="text-empty"> {t(`${DATATABLE_NAMESPACE}noData`)}</div>
      </>
    );
    let header = (
      <div className="col-md-6 float-right">
        <span className="input--hoshi float-right">
          <InputText
            className="p-column-filter"
            type="search"
            onInput={e => this.setState({ globalFilter: e.target.value })}
          />
          <label className="p-column-title" htmlFor="input">
            <span>{t(`${DATATABLE_NAMESPACE}globalSearchPlaceholder`)}</span>
          </label>
        </span>
      </div>
    );
    let enteriesCount = this.props.value ? this.props.value.length : 0;
    let footer = t(`${DATATABLE_NAMESPACE}resultsNumber`, {
      resultsNbr: enteriesCount,
    });
    let cols = this.props.cols;
    let dynamicColumns = cols.map((col, i) => (
      <Column
        bodyClassName={col.bodyClassName}
        headerClassName={col.className}
        header={col.header}
        sortable={true}
        filter={col.filter}
        key={col.field}
        field={col.field}
        body={col.body}
        filterElement={col.filterElement}
        sortField={col.sortField}
        filterFunction={col.filterFunction}
        filterMatchMode="custom"
      />
    ));
    return (
      <DataTable
        tableClassName="table-striped"
        header={header}
        value={this.props.value}
        resizableColumns={true}
        paginator={true}
        rows={10}
        ref={this.props.reference}
        globalFilter={this.state.globalFilter}
        rowsPerPageOptions={[10, 20, 40]}
        reorderableColumns={true}
        reorderableRows={true}
        onRowReorder={this.props.onRowReorder}
        columnResizeMode={this.props.columnResizeMode}
        footer={footer}
        emptyMessage={NoDataMessage}
      >
        {dynamicColumns}
      </DataTable>
    );
  }
}

export default OurDataTableCustom;
